
import { defineComponent, onMounted, ref } from "vue";
import Web3Modal from "web3modal";
import axios from "axios";

import { ethers } from "ethers";
import { nftaddress, nftmarketaddress } from "../../config";
import NFT from "../../artifacts/contracts/NFT.sol/NFT.json";
import Market from "../../artifacts/contracts/Market.sol/NFTMarket.json";
import { NFTMarketData } from "@/interface/base";

export default defineComponent({
  name: "Home",
  components: {},
  setup() {
    var tokens = ref<Array<NFTMarketData>>([]);
    var myTokens = ref<Array<NFTMarketData>>([]);
    async function getPastEvent() {
      const web3Modal = new Web3Modal();
      const connection = await web3Modal.connect();
      const provider = new ethers.providers.Web3Provider(connection);
      const signer = provider.getSigner();

      /* next, create the item */
      // let contract = new ethers.Contract(nftaddress, NFT.abi, signer);
      // console.log(await contract.functions.tokenURI(0));
    }

    async function buyNft(nft: NFTMarketData) {
      const web3Modal = new Web3Modal();
      const connection = await web3Modal.connect();
      const provider = new ethers.providers.Web3Provider(connection);
      const signer = provider.getSigner();
      const contract = new ethers.Contract(
        nftmarketaddress,
        Market.abi,
        signer
      );

      const price = ethers.utils.parseUnits(nft.price.toString(), "ether");
      const transaction = await contract.createMarketSale(
        nftaddress,
        nft.tokenId,
        {
          value: price,
        }
      );
      await transaction.wait();
      loadNFTs();
    }
    async function loadNFTs() {
      /* create a generic provider and query for unsold market items */
      const web3Modal = new Web3Modal();
      const connection = await web3Modal.connect();
      const provider = new ethers.providers.Web3Provider(connection);
      const signer = provider.getSigner();
      const tokenContract = new ethers.Contract(nftaddress, NFT.abi, signer);
      const marketContract = new ethers.Contract(
        nftmarketaddress,
        Market.abi,
        signer
      );
      console.log("tokenContract", await tokenContract.name());
      console.log("marketContract", marketContract);
      const myData = await marketContract.fetchMyNFTs();
      console.log(myData);
      const myItems = (await Promise.all(
        myData.map(async (i) => {
          const tokenUri = await tokenContract.tokenURI(i.tokenId);
          const meta = await axios.get(tokenUri);
          let price = ethers.utils.formatUnits(i.price.toString(), "ether");
          let item = {
            price,
            tokenId: i.tokenId.toNumber(),
            seller: i.seller,
            owner: i.owner,
            sold: i.sold,
            image: meta.data,
          } as NFTMarketData;
          console.log(item);
          return item;
        })
      )) as Array<NFTMarketData>;
      myTokens.value = myItems;

      const data = (await marketContract.functions.fetchMarketItems())[0];
      console.log("datas", data);
      const items = (await Promise.all(
        data.map(async (i) => {
          const tokenUri = (await tokenContract.tokenURI(i.tokenId)) as string;
          if (tokenUri.includes("pinata")) {
            console.log("pp", tokenUri);

            return false;
          }
          console.log("nopp", tokenUri);

          const meta = await axios.get(tokenUri);
          let price = ethers.utils.formatUnits(i.price.toString(), "ether");
          let item = {
            price,
            tokenId: i.tokenId.toNumber(),
            seller: i.seller,
            owner: i.owner,
            sold: i.sold,
            image: meta.data,
          } as NFTMarketData;
          console.log(item);
          return item;
        })
      )) as Array<NFTMarketData>;
      tokens.value = items;
    }
    async function createSale() {
      const url = "https://int.dev.sw.sensestar.live/nft/nft.json";
      const web3Modal = new Web3Modal();
      const connection = await web3Modal.connect();
      const provider = new ethers.providers.Web3Provider(connection);
      const signer = provider.getSigner();

      /* next, create the item */
      let contract = new ethers.Contract(nftaddress, NFT.abi, signer);
      console.log(contract);
      let transaction = await contract.createToken(url);
      let tx = await transaction.wait();
      let event = tx.events[0];
      let value = event.args[2];
      let tokenId = value.toNumber();
      const price = ethers.utils.parseUnits("0.5", "ether");

      /* then list the item for sale on the marketplace */
      contract = new ethers.Contract(nftmarketaddress, Market.abi, signer);
      let listingPrice = await contract.getListingPrice();
      listingPrice = listingPrice.toString();

      transaction = await contract.createMarketItem(
        nftaddress,
        tokenId,
        price,
        { value: listingPrice }
      );
      await transaction.wait();
    }
    onMounted(async () => {
      console.log("mounted");
      loadNFTs();
    });
    return { createSale, loadNFTs, getPastEvent, tokens, buyNft, myTokens };
  },
});
